import {
	commands,
	ExtensionContext,
	Uri,
	window,
	workspace,
	ProgressLocation,
	RelativePattern
} from 'vscode';

import { ImageCompressor } from './compressor';
import { resultToString, resultsToString } from './utilities';
import { sendTelemetryData } from './telemetry';

const output = window.createOutputChannel('Learn: Image compression');

export async function activate(context: ExtensionContext) {
	const config = workspace.getConfiguration('learnImages');
	const maxWidth = config.get<number>('maxWidth');
	const maxHeight = config.get<number>('maxHeight');
	try {
		[
			commands.registerCommand('learn.compressImage', async (uri: Uri) => {
				window.withProgress(
					{
						location: ProgressLocation.Window
					},
					async progress => {
						const compressor = new ImageCompressor(output, progress);
						const result = await compressor.compressImage(uri.fsPath, maxWidth, maxHeight);
						const resultStr = resultToString(result);

						output.appendLine(resultStr);
						progress.report({ message: resultStr });
					}
				);
			}),
			commands.registerCommand('learn.compressImagesInFolder', async (uri: Uri) => {
				window.withProgress(
					{
						location: ProgressLocation.Window
					},
					async progress => {
						const compressor = new ImageCompressor(output, progress);
						const filePaths = await workspace.findFiles(
							new RelativePattern(uri.path, `**/*.{png,jpg,jpeg,gif,svg,webp}`)
						);
						const results = await compressor.compressImagesInFolder(
							filePaths.map(u => u.fsPath),
							maxWidth,
							maxHeight
						);
						const resultStr = resultsToString(results);

						output.appendLine(resultStr);
						progress.report({ message: resultStr });
					}
				);
			})
		].forEach(cmd => context.subscriptions.push(cmd));
	} catch (error) {
		const errorMessage = `learn-images failed to activate. Error registering commands with vscode extension context: ${error}`;
		output.appendLine(errorMessage);
		sendTelemetryData('activate', errorMessage);
	}
}

export function deactivate() {
	if (output) {
		output.hide();
		output.clear();
		output.dispose();
	}
}
